<template>
  <div class="help">
    <nav-head title="新手帮助" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="padding-25" v-html="content">
      <div>{{content}}</div>
    </div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import http from '../../tool/http'
import { Toast } from 'mint-ui'

export default {
  name: 'help',
  data: () => {
    return {
      content: ''
    }
  },
  components: {
    NavHead
  },
  created () {
    http.getUserHelp().then(data => {
      if (data) {
        this.content = data.post_content
      } else {
        Toast('新手帮助获取失败')
      }
    })
  }
}
</script>
<style scoped lang="scss">
  .help{
    background-color: $background-light-color;
    line-height: 24px;
  }
  .padding-25 {
    margin: 25px;
  }
</style>
